import React from 'react'
import Link from 'gatsby-link'

const WritingPage = () =>
  <div className='half-page'>
    <h1>📝 Writing</h1>
    <div className="indent">
      <p>Popular articles</p>
      <p>Newsletter link</p>
      <Link to="/">Go back to the homepage</Link>
    </div>
  </div>

export default WritingPage



